if (module.hot) {
    module.hot.accept(function () {
        window.location.reload()
    })
}


window.onload = () => {

    let btnToggleOpen = document.getElementById('js-btn-open')
    let btnToggleClose = document.getElementById('js-btn-close')
    let menu = document.getElementById('js-menu')

    btnToggleOpen.addEventListener('click', () => {
        menu.className += " toggle__menu--open"
    })
    btnToggleClose.addEventListener('click', () => {
        menu.className = " toggle__menu"
    })

}